import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Box,
  Button,
  Grid,
  Skeleton,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader, useAuthUser } from 'react-auth-kit'
import { Helmet } from 'react-helmet-async'
import PageTitleWrapper from 'src/components/PageTitleWrapper'
import PasswordModal from './PasswordModal'
import UserDataModal from './UserDataModal'
import WithdrawModal from './WithdrawModal'

interface UserAPIResponse extends AxiosResponse {
  id: number
  email: string
  studentId: string
  name: string
  nickname: string
  department: string
  phoneNumber: string
}

function Mypage() {
  const authHeader = useAuthHeader()
  const [userData, setUserData] = useState(null)
  const [reloadUserData, setReloadUserData] = useState(false)
  const [userDataModal, setUserDataModal] = useState(false)
  const [passwordModal, setPasswordModal] = useState(false)
  const [withdrawModal, setWithdrawModal] = useState(false)
  const { enqueueSnackbar } = useSnackbar()
  const authUser = useAuthUser()

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('md'))

  useEffect(() => {
    setUserData(null)
    axios
      .get<UserAPIResponse>(`${process.env.REACT_APP_APIHOST}/user`, {
        headers: { Authorization: authHeader() }
      })
      .then(res => setUserData(res.data))
      .catch(err =>
        enqueueSnackbar(err.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }, [reloadUserData])

  return (
    <>
      <Helmet>
        <title>사용자 정보</title>
      </Helmet>
      <PageTitleWrapper>
        <Grid container alignItems="center">
          <Grid item>
            <Typography variant="h3" component="h3" gutterBottom>
              마이페이지
            </Typography>
            <Typography variant="subtitle2">KLUB에 등록된 {authUser().nickname} 님의 정보입니다</Typography>
          </Grid>
        </Grid>
      </PageTitleWrapper>
      <Box sx={{ px: matches ? 3 : 0 }}>
        <Accordion defaultExpanded={true} square={!matches}>
          <AccordionSummary expandIcon={<ExpandMoreIcon />} aria-controls="panel-content" id="panel-header">
            <Typography variant="h4" component="h4">
              사용자 정보
            </Typography>
          </AccordionSummary>
          <AccordionDetails id="panel-content">
            <Grid container spacing={2}>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>닉네임</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.nickname}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>이메일</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.email}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>학번</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.studentId}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>이름</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.name}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>학과/학부</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.department}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
              <Grid item xs={4} md={2} sx={{ textAlign: 'right' }}>
                <Typography sx={{ fontWeight: 'bold' }}>전화번호</Typography>
              </Grid>
              <Grid item xs={8} md={10}>
                {userData ? (
                  <Typography>{userData.phoneNumber}</Typography>
                ) : (
                  <Skeleton variant="text" width={150} sx={{ fontSize: 14 }} />
                )}
              </Grid>
            </Grid>
            <Box
              sx={{
                display: 'flex',
                flexDirection: matches ? 'row' : 'column',
                justifyContent: matches ? 'flex-end' : 'center',
                alignItems: 'center',
                marginTop: 2
              }}
            >
              <Button fullWidth={!matches} onClick={() => setUserDataModal(true)}>
                정보 입력 / 수정
              </Button>
              <UserDataModal
                open={userDataModal}
                setOpen={setUserDataModal}
                onClose={() => setUserDataModal(false)}
                setReloadUserData={setReloadUserData}
                nickname={userData?.nickname}
                name={userData?.name}
                department={userData?.department}
                phoneNumber={userData?.phoneNumber}
              />
              <Button fullWidth={!matches} onClick={() => setPasswordModal(true)}>
                비밀번호 수정
              </Button>
              <PasswordModal open={passwordModal} setOpen={setPasswordModal} onClose={() => setPasswordModal(false)} />
              <Button color="error" fullWidth={!matches} onClick={() => setWithdrawModal(true)}>
                회원 탈퇴
              </Button>
              <WithdrawModal open={withdrawModal} handleClose={() => setWithdrawModal(false)} />
            </Box>
          </AccordionDetails>
        </Accordion>
      </Box>
    </>
  )
}

export default Mypage
