import { Box, Button, Modal, styled, TextField as MuiTextField, TextFieldProps, Typography } from '@mui/material'
import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'

interface PasswordModalProps {
  open: boolean
  setOpen: (open: boolean) => void
  onClose: (event: any, reason: 'backdropClick' | 'escapeKeyDown') => void
}

interface UpdatePasswordAPIResponse extends AxiosResponse {
  currentPassword: string
  newPassword: string
}

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

const ModalBox = styled(Box)(
  ({ theme }) => `
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 360px;
    width: 90%;
    background-color: #fff;
    box-shadow: 24;
    padding: ${theme.spacing(4)};
  `
)

function PasswordModal({ open, setOpen, onClose }: PasswordModalProps) {
  const authHeader = useAuthHeader()
  const [values, setValues] = useState({ currentPassword: null, newPassword: null })
  const { enqueueSnackbar } = useSnackbar()

  // const handleChange = event => {
  //   if (event.target.id === 'confirm-password') setConfirmPassword(event.target.value)
  //   else {
  //     const newValues = {
  //       ...values,
  //       [event.target.name]: event.target.value
  //     }
  //     setValues(newValues)
  //   }
  // }
  const handleChange = event => {
    if (event.target.type === 'password') {
      const newPassword = document.getElementById('new-password') as HTMLInputElement
      const confirmPassword = document.getElementById('confirm-password') as HTMLInputElement
      if (newPassword.value !== confirmPassword.value) confirmPassword.setCustomValidity("Passwords don't match")
      else confirmPassword.setCustomValidity('')
    }
    if (event.target.id !== 'confirm-password') {
      const newValues = {
        ...values,
        [event.target.name]: event.target.value
      }
      setValues(newValues)
    }
  }
  const handleSubmit = e => {
    e.preventDefault()
    axios
      .put<UpdatePasswordAPIResponse>(`${process.env.REACT_APP_APIHOST}/user/password`, values, {
        headers: { Authorization: authHeader() }
      })
      .then(() => {
        enqueueSnackbar('비밀번호를 수정했어요.', { variant: 'success' })
        setOpen(false)
      })
      .catch(err =>
        enqueueSnackbar(err.response.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      aria-labelledby="password-modal-title"
      aria-describedby="password-modal-description"
    >
      <ModalBox>
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          비밀번호 수정
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField
            id="current-password"
            name="currentPassword"
            type="password"
            label="현재 비밀번호"
            onChange={handleChange}
          />
          <TextField id="new-password" name="newPassword" type="password" label="새 비밀번호" onChange={handleChange} />
          <TextField id="confirm-password" type="password" label="비밀번호 확인" onChange={handleChange} />
          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            비밀번호 수정
          </Button>
          <Button variant="outlined" fullWidth onClick={() => setOpen(false)} sx={{ mt: 1 }}>
            취소
          </Button>
        </Box>
      </ModalBox>
    </Modal>
  )
}

export default PasswordModal
