import { Dialog, Box, Typography, Button } from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { FC } from 'react'
import { useAuthHeader, useAuthUser, useSignOut } from 'react-auth-kit'

interface WithdrawModalProps {
  open: boolean
  handleClose(): void
}

const WithdrawModal: FC<WithdrawModalProps> = ({ open, handleClose }) => {
  const authUser = useAuthUser()
  const authHeader = useAuthHeader()
  const signOut = useSignOut()

  const { enqueueSnackbar } = useSnackbar()

  const handleSubmit = () => {
    axios
      .delete(`${process.env.REACT_APP_APIHOST}/user?userId=${authUser().id}`, {
        headers: { Authorization: authHeader() }
      })
      .then(({ data }) => {
        if (data.message !== 'ok') throw new Error()

        enqueueSnackbar('서비스 탈퇴가 완료되었습니다. 다시 만나요!', { variant: 'success' })
        signOut()
      })
      .catch(err => {
        enqueueSnackbar(err.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      })
  }

  return (
    <Dialog open={open} onClose={handleClose} maxWidth="xs">
      <Box p={3}>
        <Typography color="error" fontWeight="bold">
          활동 중이신 동아리가 있다면, 회원님의 개인정보는 해당 동아리 운영진만 조회 가능한 형태로 서버에 보존됩니다.
        </Typography>
        <Typography fontWeight="bold">
          개인정보 완전 삭제를 원하신다면, 활동 중이신 동아리를 모두 탈퇴(또는 해당 동아리 운영진에게 탈퇴 요청)하시기
          바랍니다.
        </Typography>
        <br />
        <Typography variant="h4">유의사항</Typography>
        <Typography>1. 활동 중이신 동아리가 없다면, 개인정보는 영업일 기준 최대 7일 이내에 자동 삭제됩니다.</Typography>
        <Typography>
          2. 삭제 처리 후에는 회원님의 개인정보를 복원할 수 없으며, 추후 저희 서비스를 다시 이용하고자 하시는 경우
          재가입하셔야 합니다.
        </Typography>

        <Box display="flex" justifyContent="space-evenly" mt={2}>
          <Button variant="outlined" onClick={handleClose} sx={{ paddingX: 4 }}>
            취소
          </Button>
          <Button variant="contained" color="error" onClick={handleSubmit} sx={{ paddingX: 4 }}>
            확인
          </Button>
        </Box>
      </Box>
    </Dialog>
  )
}

export default WithdrawModal
